import React from 'react'
import './App.css'

import NameTag from './components/NameTag'
import Compliment from './components/Compliment'

class App extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      name: 'name',
      show: false
    }

    this.callback = this.callback.bind(this)
  }
  callback (newName) {
    this.setState({
      name: newName,
      show: true
    })
  }

  render () {
    return (
      <div className='App'>
        <NameTag callback={this.callback} visible={!this.state.show} />
        <Compliment name={this.state.name} visible={this.state.show} />
      </div>
    )
  }
}

export default App
