import React from 'react'
import Typing from 'react-typing-animation'
import './Compliment.css'

import firebaseApp from '../utilities/Firebase'

class Compliment extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      loaded: false,
      complimentList: []
    }
  }

  componentDidMount () {
    var db = firebaseApp.firestore()
    var compsRef = db.collection('compliments').where('popularity', '<', 75).orderBy('popularity')
    var compliments = []
    compsRef.get().then((querySnapshot) => {
      querySnapshot.forEach((doc) => {
        compliments.push(doc.data().value)
        this.setState({
          complimentList: compliments
        })
      })
    })
  }

  render () {
    if (this.props.visible) {
      return (
        <div className='compliment'>
          <Typing>
            <p>Hi, {this.props.name}.</p>
            <p>{this.state.complimentList[0]}</p>
          </Typing>
        </div>
      )
    } else {
      return null
    }
  }
}

export default Compliment
