import React from 'react'

import './NameTag.css'

class NameTag extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      name: ''
    }

    this.updateName = this.updateName.bind(this)
  }

  updateName (event) {
    this.setState({
      name: event.target.value
    })
  }

  render () {
    if (this.props.visible) {
      return (
        <div className='nametag'>
          <h1>What is your name?</h1>
          <div>
            <input onChange={this.updateName} value={this.state.name} type='text' placeholder="Don't be a stranger!" />
          </div>
          <div>
            <button className='submit-button' onClick={() => this.props.callback(this.state.name)}>Submit</button>
          </div>
        </div>
      )
    } else {
      return null
    }
  }
}

export default NameTag
